import { Component, OnInit } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController} from 'ionic-angular';
import { Item } from '../../models/item';
import { ItemService } from '../../service/item';

/**
 * Generated class for the Daybid page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-daybid',
  templateUrl: 'daybid.html',
  providers: [ItemService]
})
export class DaybidPage implements OnInit {

  segment: string;
  todayItemList: Item[];
  today_pnum: number = 1;
  today_pqty: number = 5;

  constructor(public navCtrl: NavController, public navParams: NavParams, private itemService: ItemService, private toaCtrl: ToastController) {

  }

  ionViewDidLoad() {
    this.segment = 'today';
  }

  ngOnInit() {
    this.getTodayItem();
  }

  enterItem(item: Item) {
    this.navCtrl.push('ItemPage', { item: item });
  }

  enterMessagePage() {
    this.navCtrl.push('MessagePage');
  }

  getTodayItem() {
    this.itemService.getTodayItemList(this.today_pnum, this.today_pqty).subscribe(itemList => {
      this.today_pnum++;
      this.todayItemList = itemList;
    });
  }

  getMoreTodayItem(infiniteScroll?) {//上拉加载
    this.itemService.getTodayItemList(this.today_pnum, this.today_pqty).subscribe(itemList => {
      if (itemList.length > 0) {
        this.today_pnum++;
        if (infiniteScroll) {
          infiniteScroll.complete();
        }
        this.todayItemList = this.todayItemList.concat(itemList);
      } else {
        if (infiniteScroll) {
          infiniteScroll.complete();
          infiniteScroll.enable(false);
        }
        this.toaCtrl.create({
          message: '已经到底了o(╯□╰)o',
          duration: 1000,
          //showCloseButton: true,
          dismissOnPageChange: true
        }).present();
      }
    }, error => {
      infiniteScroll.complete();
      this.toaCtrl.create({
        message: '好像有什么不对劲o(╯□╰)o',
        duration: 1000,
        //showCloseButton: true,
        dismissOnPageChange: true
      }).present();
    });
  }

}



