import { Injectable } from '@angular/core';
import { Http, Response, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/switchMap';
import { Item } from '../models/item';
import { Setting } from '../app/app.setting';
import { WebSocketProvider } from '../providers/web-socket';
import { Device } from '@ionic-native/device';

@Injectable()
export class ItemService {
  private itemDetailUrl = this.setting.apiTestHost + '/artradeApp/catalogueinfo/getcatalogueinfobylotid.action';

  private todayItemListUrl = this.setting.apihost + '/listings/endOfDaylist';
  private itemCollectionUrl = this.setting.apihost + '/personal/collectAuctionlist';
  private myBiddingItemListUrl = this.setting.apihost + '/personal/myBidding';
  private itemSearchUrl = this.setting.apihost + '/search/searchItems';
  private caredItemModUrl = this.setting.apihost + '/personal/caredItemMod';
  private unpaidItemUrl = this.setting.apihost + '/personal/unpaidItem';

  constructor(private http: Http, private setting: Setting, private ws: WebSocketProvider, private device: Device) {

  }
  /** 监听出价信息 */
  listenBidings(): Observable<MessageEvent> {
    return this.ws.listen('/chat/demo');
  }

  // 关闭ws连接
  disListenBidings() {
    this.ws.unsubscribe();
  }



  // 根据专场id获取拍品列表


  getTodayItemList(pnum: number, pqty: number): Observable<Item[]> {//获取今日结束的拍品列表
    let params = new URLSearchParams();
    params.set('Parmeters', '{"InputData":[{"APIName":"endOfDaylist","Parameters":[{"imei":"12365478912345","userId":"1367197261","cateId":"0","pnum":"' + pnum + '","pqty":"' + pqty + '","porder":""}]}]}');
    return this.http.get(this.todayItemListUrl, { search: params })
      .map(res => res.json().OutputData.Data || [])
      .catch(this.handleError);
  }

  getCollectionItemList(pnum: number, pqty: number): Observable<Item[]> {//获取已收藏拍品列表
    let params = new URLSearchParams();
    params.set('Parmeters', '{"InputData":[{"APIName":"collectAuctionlist","Parameters":[{"imei":"12365478912345","userId":"1367197261","cateId":"","pnum":"' + pnum + '","pqty":"' + pqty + '","porder":"td"}]}]}');
    return this.http.get(this.itemCollectionUrl, { search: params })
      .map(res => res.json().OutputData.Data || [])
      .catch(this.handleError);
  }

  getItemDetail(lotId: number, saleRoomId: number, memberId?: number): Observable<Item> {//根据拍品id获取拍品详情
    let params = new URLSearchParams();
    let paramsObject = {
      dataStr: {
        imei: this.device.uuid || '0',
        memberId: memberId || '',
        lotId: lotId,
        saleRoomId: saleRoomId,
        showRoom: ''
      }
    };
    params.set('Parmeters', JSON.stringify(paramsObject));
    return this.http.get(this.itemDetailUrl, { search: params })
      .map(res => {
        let item = res.json().OutputData || {};
        if (item.sPicPath) {
          item.sPicPath = item.sPicPath.split(',');
        }
        return item;
      })
      .catch(this.handleError);
  }

  getMyBiddingItemList(pnum: number, pqty: number, type: string): Observable<Item> {//获取用户竞拍拍品列表 ,type类型：leading/outing
    let params = new URLSearchParams();
    params.set('Parmeters', '{"InputData":[{"APIName":"myBidding","Parameters":[{"imei":"12365478912345","userId":"1367197261","type":"' + type + '","pnum":"' + pnum + '","pqty":"' + pqty + '"}]}]}]}')
    return this.http.get(this.myBiddingItemListUrl, { search: params })
      .map(res => res.json().OutputData.Data || [])
      .catch(this.handleError);
  }

  getUnpaidItemList(pnum: number, pqty: number, duration: string): Observable<Item> {
    let params = new URLSearchParams();
    params.set('Parmeters', '{"InputData":[{"APIName":"unpayedItem","Parameters":[{"imei":"12365478912345","userId":"1367197261","cateId":"0","pnum":"' + pnum + '","pqty":"' + pqty + '","porder":""}]}]}');
    return this.http.get(this.unpaidItemUrl, { search: params })
      .map(res => res.json().OutputData.Data || [])
      .catch(this.handleError);
  }

  getSearchItems(pnum: number, pqty: number, searchStr: string): Observable<Item[]> {//拍品模糊查询 ,searchStr：模糊查询输入
    let params = new URLSearchParams();
    params.set('Parmeters', '{"InputData":[{"APIName":"searchItems","Parameters":[{"imei":"12365478912345","userId":"1367197261","srchStr":"' + searchStr + '","pnum":"' + pnum + '","pqty":"' + pqty + '","porder":"td"}]}]}]}')
    return this.http.get(this.itemSearchUrl, { search: params })
      .map(res => res.json().OutputData.Data || [])
      .catch(this.handleError);
  }

  caredItemMod(type: string, itemId: number): Observable<boolean> {//拍品收藏管理 ,type: care/uncare  收藏/取消收藏
    let params = new URLSearchParams();
    params.set('Parmeters', '{"InputData":[{"APIName":"caredItemMod","Parameters":[{"imei":"12365478912345","userId":"1367197261","itemnum":"' + itemId + '","type":"' + type + '"}]}]}]}')
    return this.http.get(this.caredItemModUrl, { search: params })
      .map(res => !!res.json().OutputData.Success)
      .catch(this.handleError);
  }


  private handleError(error: Response | any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Observable.throw(errMsg);
  }
}



